import { FetchAddressFormFieldsQuery } from '@/checkout/__generated__/types';
import { fetchGraphQl } from '@/checkout/api';
import { handleNetworkError } from '@/checkout/lib/network-error';

import { fetchAddressFormFieldsQuery } from './graphql/fetchAddressFormFields.graphql';

type CustomerAttributesItems = Extract<
  FetchAddressFormFieldsQuery['attributesForm']['items'][number],
  { __typename?: 'CustomerAttributeMetadata' }
>;
type FetchFieldsQuery = {
  attributesForm: Omit<
    FetchAddressFormFieldsQuery['attributesForm'],
    'items'
  > & { items: CustomerAttributesItems[] };
};

export const fetchAddressFormFields = async () => {
  const { data, errors } = await fetchGraphQl<FetchFieldsQuery>(
    fetchAddressFormFieldsQuery,
    { method: 'GET', cache: 'no-cache' }
  ).catch(handleNetworkError);

  if (errors) throw Error(errors.map((e: any) => e.message).join(' '));

  return data?.attributesForm?.items || [];
};

export type FormFields = Awaited<ReturnType<typeof fetchAddressFormFields>>;
